@import '../../assets/css/variable.less';

.trading-mining {
  border-top: 1px solid @divider_color;

  .check {
    display: flex;
    margin-top: 18px;
    margin-left: 45px;

    div {
      width: 88px;
      height: 31px;
      background: rgba(85, 119, 253, 0.1);
      margin-left: 7px;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: @whiteFontColor;
      cursor: pointer;

      a {
        text-decoration: none;
        color: @whiteFontColor;
      }
    }

    .now {
      background: #5577FD;
    }
  }

  .upcoming-box {
    margin-top: 28px;
    margin-left: 50px;
  }

  .now-prom {
    width: 1250px;
    // height: 1444px;
    // margin: 0 auto;
    // margin-top: 44px;
    margin-top: 28px;
    margin-left: 50px;
    // border: 1px solid @divider_color;
    // background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);

  }

  .finished-box {
    width: 1250px;
    margin: 0 auto;
    margin-top: 4px;

    .finished-epoch {
      height: 360px;
      border-bottom: 1px solid #203B60;
      margin-top: 40px;

      .finished-epoch-box {
        height: 320px;
        background: url('./img/finished-bg-md.png');
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: relative;

        .finished-logo {
          position: absolute;
          background-color: @primary_color;
          top: 1px;
          right: 1px;
          width: 160px;
          height: 56px;
          border-radius: 0 0 0 20px;
          font-size: 24px;
          color: #FFFFFF;
          text-align: center;
          line-height: 56px;
        }

        .epoch-box {
          padding-top: 70px;
          padding-left: 214px;
          color: @defaultFontColor;
          font-weight: normal;

          .epoch-box-title {
            font-size: 32px;

          }

          .epoch-box-time {
            margin-top: 8px;
          }

          .epoch-value {
            font-size: 72px;
            font-weight: 600;
            margin-top: 37px;
          }
        }
      }
    }
  }

  .detailed-rules {
    padding-top: 19px;
    padding-bottom: 19px;
  }
}

@media only screen and (max-width:759px) {
  .trading-mining {
    .now-prom {
      width: 375px;
      // height: 2672px;
      border: 0;
    }

    .finished-box {
      width: 100%;
      margin: 0 auto;
      margin-top: 4px;

      .finished-epoch {
        width: 100%;
        height: 360px;

        .finished-epoch-box {
          width: 327px;
          margin: 0 auto;
          background: url('./img/finished-bg-m.png');
          height: 331px;
          .epoch-box {
            padding-left: 21px;
            padding-top: 128px;

            .epoch-value {
              font-size: 50px;
            }
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 760px) and (max-width:1139px) {
  .trading-mining {
    .now-prom {
      width: 760px;
    }

    .finished-box {
      width: 100%;
      margin: 0 auto;
      margin-top: 4px;

      .finished-epoch {
        width: 100%;
        height: 360px;

        .finished-epoch-box {
          width: 730px;
          margin: 0 auto;
          background: url('./img/finished-bg-xs.png');
          .epoch-box {
            padding-left: 193px;
          }
        }
      }
    }

  }
}

@media only screen and (min-width : 1140px) and (max-width:1270px) {
  .trading-mining {
    .now-prom {
      width: 1135px;
      border: 0;
      border-bottom: 1px solid @defaultFontColor;
    }

    .finished-box {
      width: 100%;
      margin: 0 auto;
      margin-top: 4px;

      .finished-epoch {
        width: 100%;
        margin: 0 auto;
        margin-top: 40px;

        .finished-epoch-box {
          width: 1088px;
          margin: 0 auto;
          background: url('./img/finished-bg-sm.png');

        }
      }
    }

  }
}